namespace $ { export class $mol_scroll extends $mol_view {

	/// minimal_height 0
	minimal_height() {
		return 0
	}

	/// scroll_top?val 0
	@ $mol_mem()
	scroll_top( val? : any ) {
		return ( val !== void 0 ) ? val : 0
	}

	/// scroll_left?val 0
	@ $mol_mem()
	scroll_left( val? : any ) {
		return ( val !== void 0 ) ? val : 0
	}

	/// field * 
	/// 	scrollTop <= scroll_top?val 
	/// 	scrollLeft <= scroll_left?val
	field() {
		return ( { ...super.field() , 
			"scrollTop" :  <any> this.scroll_top() ,
			"scrollLeft" :  <any> this.scroll_left() ,
		} )
	}

	/// event_scroll?event null
	@ $mol_mem()
	event_scroll( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// event * 
	/// 	scroll?event <=> event_scroll?event 
	/// 	overflow?event <=> event_scroll?event 
	/// 	underflow?event <=> event_scroll?event
	event() {
		return ( { ...super.event() , 
			"scroll" :  ( event? : any )=>  <any> this.event_scroll( event ) ,
			"overflow" :  ( event? : any )=>  <any> this.event_scroll( event ) ,
			"underflow" :  ( event? : any )=>  <any> this.event_scroll( event ) ,
		} )
	}

} }

